<template>
  <div>
    <div class="row">
      <!--  -->
      <div class="search-bar">
        <label for="typeSelect"></label>
        <input type="text" v-model="searchQuery" placeholder="" />
      </div>

      <!--  -->
      <div class="select-box">
        <label for="typeSelect"></label>
        <select id="typeSelect" v-model="selectedType">
          <option value="All"></option>
          <option value="Attack">Attack</option>
          <option value="Balance">Balance</option>
          <option value="Cross">Cross</option>
          <option value="Guard">Guard</option>
          <option value="Rush">Rush</option>
          <!--  -->
        </select>
      </div>
    </div>

    <div class="row">
      <!-- Main -->
      <div class="select-box">
        <label for="mainSelect">OP</label>
        <select id="mainSelect" v-model="selectedMain">
          <option value="All"></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value="HP">HP</option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <!-- Main -->
        </select>
      </div>

      <!-- Sub -->
      <div class="select-box">
        <label for="subSelect">OP</label>
        <select id="subSelect" v-model="selectedSub">
          <option value="All"></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value="HP">HP</option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <!-- Sub -->
        </select>
      </div>
    </div>
    <table>
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in paginatedData" :key="item.Name">
          <td><img :src="item.Image" alt="Image" width="56" height="56" /></td>
          <td>
            <a @click="showDetails(item)">{{ item.Name }}</a>
          </td>
          <td>{{ item.level }}</td>
          <td>{{ item.type }}</td>
          <td>
            {{ item.Main_Option }}
            <br />
            {{ item.Sub_Option }}
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <div class="pagination-container">
      <div class="pagination">
        <button @click="prevPage" :disabled="currentPage === 1"></button>
        <span> {{ currentPage }} / {{ totalPages }}</span>
        <button @click="nextPage" :disabled="currentPage === totalPages">
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    jsonData: Array,
  },
  data() {
    return {
      currentPage: 1,
      itemsPerPage: 25,
      searchQuery: "",
      selectedType: "All", // 
      selectedMain: "All", // Main
      selectedSub: "All", // Sub
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    paginatedData() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.filteredData.slice(startIndex, endIndex);
    },
    filteredData() {
      return this.jsonData.filter(
        (item) =>
          item.Name.toLowerCase().includes(this.searchQuery.toLowerCase()) &&
          (this.selectedType === "All" || item.type === this.selectedType) &&
          (this.selectedMain === "All" ||
            item.Main_Option === this.selectedMain) &&
          (this.selectedSub === "All" || item.Sub_Option === this.selectedSub),
      );
    },
  },
  watch: {
    // currentPage 
    currentPage: {
      handler: 'sortTable', // currentPage  sortTable 
      immediate: true, // 
    },
    // selectedType, selectedMain, selectedSub 
    selectedType: 'resetPageAndSort',
    selectedMain: 'resetPageAndSort',
    selectedSub: 'resetPageAndSort',
  },
  methods: {
    sortTable(column) {
      this.filteredData.sort((a, b) => {
        const aValue = a[column];
        const bValue = b[column];
        return aValue.localeCompare(bValue);
      });
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    resetPageAndSort() {
      this.currentPage = 1;  // currentPage 
      this.sortTable('Name'); // 
    },
    showDetails(item) {
      // 
      this.$router.push({ name: 'itemDetail', params: { itemName: item.Name } });
    },
  },
};
</script>

